    object-fit: cover;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    pointer-events: none;
    border: none 0;
    transition: all 0.15s ease;
}

.demo-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 0;
    visibility: hidden;
    z-index: 999999;
    transition: all 0.15s ease;
}

.demo-popup.js-active {
    opacity: 1;
    visibility: visible;
    transition: all 0.15s ease;
}

.demo-popup .popup-close {
    position: absolute;
    left: 20px;
    top: 20px;
    width: 25px;
    cursor: pointer;
    transition: all 0.15s ease;
}

.demo-popup .popup-close:hover {
    transform: scale(1.2);
    transition: all 0.15s ease;
}

.demo-popup .popup-close:active {
    transform: scale(1);
    transition: all 0.15s ease;
}

.demo-popup .demo-popup-content {
    position: absolute;
    width: 70%;
    height: 70%;
    max-width: 1200px;
    max-height: 700px;
    margin: auto;
    left: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.15s ease;
}

.demo-popup .demo-popup-content iframe {
    width: 100%;
    height: 100%;
    border: none 0;
}

.demo-popup .demo-popup-content .tab {
    width: 100%;
    height: 100%;
    display: none;
}

.demo-popup .demo-popup-content .tab.js-active {
    display: block;
}

.demo-popup .demo-popup-content ul {
    position: absolute;
    top: -70px;
    width: 100%;
    text-align: center;
}

.demo-popup .demo-popup-content ul li {
    display: inline-block;
    margin: 0 20px;
}

.demo-popup .demo-popup-content ul li span {
    display: block;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 15px;
    cursor: pointer;
    border-bottom: solid 3px transparent;
    transition: all 0.15s ease;
}

.demo-popup .demo-popup-content ul li.js-active span,
.demo-popup .demo-popup-content ul li:hover span {
    border-color: #fff;
    transition: all 0.15s ease;
}

.field-error input,
.field-error select,
.field-error textarea {
    border: solid 2px #ff5831;
}

.field-message {
    display: block;
    font-size: 14px;
    font-weight: bold;
    margin-top: -15px;
    margin-bottom: 20px;
    color: #ff5831;
}

.products {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 40px;
    margin-bottom: 70px;
}

.strike {
    text-decoration: line-through;
}

.slider-text p {
    margin: 40px 0;
    font-size: 25px;
}

.products li {
    width: 23%;
    text-align: center;
}

.products li .image {
    width: 100%;
    height: 250px;
    background: #eee;
    margin-bottom: 15px;
}

.products li .image img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.products li .content h2 {
    font-size: 16px;
    font-weight: bold;
    margin: 10px 0;
    line-height: 1.3;
}

.products li .content .icon-star {
    font-size: 17px;
    font-weight: bold;
}

.products li .content .icon-star.full {
    color: orange;
}

.products li .buy {
    font-size: 17px;
}

.products li .buy .add-to-cart {
    display: block;
    color: #fff;
    background-color: #333;
    padding: 10px;
    font-size: 14px;
    font-weight: bold;
    transition: all 0.15s ease;
    cursor: pointer;
    max-width: 150px;
    margin: auto;
    margin-top: 20px;
}

.products li .buy .add-to-cart:hover {
    opacity: 0.8;
    transition: all 0.15s ease;
}

.products li .buy .add-to-cart:active {
    opacity: 1;
    transition: all 0.15s ease;
}

@media screen and (max-width: 1600px) {
    .background-video {
        height: 550px;
    }
    .slider-text h2 {
        font-size: 75px;
    }
    .slider-text>p {
        font-size: 23px;
    }
    .slider-area.style-1 {
        padding-top: 70px;
        padding-bottom: 250px;
    }
}

@media screen and (max-width: 1200px) {
    header,
    .topbar,
    .brands,
    .overlaps {
        padding: 0 30px;
    }
}

@media screen and (max-width: 990px) {
    header {
        padding-top: 10px;
    }
    .products li {
        width: 49%;
        text-align: center;
        margin-bottom: 30px;
    }
    .shop-area {
        margin-top: -50px;
    }
    .shopping-cart.ml-30 {
        margin-right: 0 !important;
    }
    .shopping-cart {
        padding-right: 0 !important;
    }
    .user {
        display: none !important;
    }
    .logo img {
        width: 100px !important;
    }
    .main-menu ul li {
        margin-left: 10px;
    }
    .cart-user-language .user {
        padding-right: 0 !important;
    }
    .main-menu ul li a {
        font-size: 13px;
    }
    .cart-user-language {
        margin: 0;
    }
    .topbar {
        display: none;
    }
    .blog-area .col-md-3.col-sm-6 {
        margin-bottom: 30px;
    }
}

@media screen and (max-width: 767px) {
    .slider-text>h3 {
        margin-top: 40px;
    }
    .overlaps {
        flex-direction: column;
    }
    .overlaps li {
        width: 100%;
        margin-bottom: 30px;
    }
    .table-content table {
        margin: 0;
    }
    .overlaps li:last-child {
        margin-bottom: 0;
    }
    body .slider-area.style-1 {
        padding: 69px 0 50px;
        padding-bottom: 120px;
        padding-top: 50px;
    }
    body .slider-text h2 {
        font-size: 30px;
    }
    .demo-popup .demo-popup-content ul li span {
        font-size: 15px;
    }
}

@media screen and (max-width: 700px) {
    header .main-menu {
        display: none;
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 99999;
        padding-top: 30px;
        background-color: #121212;
        overflow: auto;
        text-align: center;
    }
    header .main-menu .mobile-menu-close {
        display: block;
        position: absolute;
        left: 15px;
        top: 15px;
        width: 20px;
        cursor: pointer;
    }
    header .main-menu .menu li {
        display: block;
        margin: 25px 0;
        padding: 0;
    }
    header .main-menu .menu li a {
        font-size: 17px;
        color: #fff;
    }
    header .main-menu .menu li i,
    header .main-menu .menu li ul {
        display: none !important;
    }
    header.js-active .main-menu {
        display: block;
    }
    .logo {
        display: inline-block;
    }
    .mobile-menu-trigger {
        display: inline-block;
        width: 20px;
        margin-right: 10px;
        cursor: pointer;
    }
    .mobile-menu-trigger img {
        display: block;
    }
}

@media screen and (max-width: 500px) {
    .background-video {
        height: 350px;
    }
    .background-video h3 {
        font-size: 35px;
    }
    .user {
        display: none;
    }
    .products li .image {
        height: 150px;
    }
    body .slider-text h2 {
        font-size: 25px;
    }
    body .slider-area.style-1 {
        padding-top: 50px;
        padding-bottom: 70px;
    }
    .owl-carousel .owl-item img {
        max-height: 25px !important;
        max-width: 100px !important;
    }
    .demo-popup .demo-popup-content ul li {
        display: block;
        line-height: 1;
    }
    .demo-popup .demo-popup-content ul li span {
        font-size: 14px;
        padding: 8px 15px;
    }
    .demo-popup .demo-popup-content {
        width: 80%;
    }
    .demo-popup .popup-close {
        width: 20px;
        left: 15px;
        top: 15px;
    }
    .slidebox {
        width: 230px;
        height: 120px;
    }
}